.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 190px;
    padding: 20px;
    color: white;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/*CONTACT LIST CSS*/

.picture{
    border-radius: 50%;
    height: 60px;
    width: 60px;
    display: inline-block;
}

.panel-default > .panel-heading
{
    color: #FFF;
    background-color: #035384;
    border-color: #ddd;
}

input[type="text"] {
    border: 1px solid #cccccc;
    padding: 5px;
    border-radius: 6px;
}

.wrapper-contact {
    margin-bottom: 20px;
}

.contact-data {
    margin-bottom: 30px;
    font-weight: bold;
}

.delete-data {
    font-weight: bold;
    margin: 15px;
}

.navbar { background-color: #339AD5; color: #FFF; }
li.hover:hover { background-color: #333333; }
.navbar-default .navbar-nav>li>a { color: #FFF; }
.navbar-default .navbar-nav>li>a:hover { color: #FFF; }
.navbar-default .navbar-brand { color: #FFF; }
.navbar-default .navbar-brand:hover { color: #FFF; }
.dropdown-menu>li>a:hover { background-color: #333333; color: #FFF; }
.navbar-default .navbar-nav>.active>a, .navbar-default .navbar-nav>.active>a:hover, .navbar-default .navbar-nav>.active>a:focus { background-color: #333333; color: #FFF; }
.navbar-default .navbar-nav>.open>a, .navbar-default .navbar-nav>.open>a:hover, .navbar-default .navbar-nav>.open>a:focus { background-color: #333333; color: #FFF; }
.navbar-toggle { background-color: #fff; }

/*FOOTER CSS*/

.footer { padding-top:15px; padding-left:20px; text-align: center; }
.navbar { background-color: #339AD5; color: #FFF; }




